/**
 * Join Our Team Page JavaScript
 * 
 * This file contains the JavaScript for the Join Our Team page.
 */

// Import components
import Accordion from '../components/accordion';

/**
 * Initialize the page
 */
function initPage() {
  console.log('Join Our Team page initialized');
  
  // Initialize accordion
  Accordion.init({
    triggerSelector: '.faq-question',
    contentSelector: '.faq-answer',
    closeOthers: true,
    animateHeight: true
  });
  
  // Set up application form validation
  setupFormValidation();
  
  // Set up file upload preview
  setupFileUploadPreview();
}

/**
 * Set up application form validation
 */
function setupFormValidation() {
  const applicationForm = document.querySelector('.join-team-form');
  
  if (applicationForm) {
    applicationForm.addEventListener('submit', function(e) {
      // Prevent default form submission
      e.preventDefault();
      
      // Validate form
      if (validateForm(applicationForm)) {
        // Show success message
        showSubmissionMessage(true, 'Your application has been submitted successfully! We will contact you soon.');
        
        // Reset form
        applicationForm.reset();
        
        // Clear file upload previews
        clearFileUploadPreviews();
      } else {
        // Show error message
        showSubmissionMessage(false, 'Please fill in all required fields correctly.');
      }
    });
    
    // Add input validation on blur
    const requiredInputs = applicationForm.querySelectorAll('input[required], textarea[required], select[required]');
    requiredInputs.forEach(input => {
      input.addEventListener('blur', function() {
        validateInput(this);
      });
    });
    
    // Add email validation
    const emailInput = applicationForm.querySelector('input[type="email"]');
    if (emailInput) {
      emailInput.addEventListener('blur', function() {
        validateEmail(this);
      });
    }
  }
  
  /**
   * Validate the entire form
   */
  function validateForm(form) {
    let isValid = true;
    
    // Validate required inputs
    const requiredInputs = form.querySelectorAll('input[required], textarea[required], select[required]');
    requiredInputs.forEach(input => {
      if (!validateInput(input)) {
        isValid = false;
      }
    });
    
    // Validate email
    const emailInput = form.querySelector('input[type="email"]');
    if (emailInput && !validateEmail(emailInput)) {
      isValid = false;
    }
    
    return isValid;
  }
  
  /**
   * Validate a single input
   */
  function validateInput(input) {
    const value = input.value.trim();
    const isValid = value !== '';
    
    if (isValid) {
      input.classList.remove('invalid');
      input.classList.add('valid');
    } else {
      input.classList.remove('valid');
      input.classList.add('invalid');
    }
    
    return isValid;
  }
  
  /**
   * Validate email format
   */
  function validateEmail(input) {
    const value = input.value.trim();
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const isValid = emailRegex.test(value);
    
    if (isValid) {
      input.classList.remove('invalid');
      input.classList.add('valid');
    } else {
      input.classList.remove('valid');
      input.classList.add('invalid');
    }
    
    return isValid;
  }
  
  /**
   * Show submission message
   */
  function showSubmissionMessage(isSuccess, message) {
    const messageContainer = document.querySelector('.form-submission-message');
    
    if (messageContainer) {
      messageContainer.textContent = message;
      messageContainer.className = 'form-submission-message';
      messageContainer.classList.add(isSuccess ? 'success' : 'error');
      messageContainer.style.display = 'block';
      
      // Scroll to message
      messageContainer.scrollIntoView({ behavior: 'smooth', block: 'center' });
      
      // Hide message after 5 seconds if it's a success message
      if (isSuccess) {
        setTimeout(() => {
          messageContainer.style.display = 'none';
        }, 5000);
      }
    }
  }
}

/**
 * Set up file upload preview
 */
function setupFileUploadPreview() {
  const resumeInput = document.querySelector('#resume');
  const coverLetterInput = document.querySelector('#cover-letter');
  
  if (resumeInput) {
    resumeInput.addEventListener('change', function() {
      createFilePreview(this, 'resume-preview');
    });
  }
  
  if (coverLetterInput) {
    coverLetterInput.addEventListener('change', function() {
      createFilePreview(this, 'cover-letter-preview');
    });
  }
  
  /**
   * Create file upload preview
   */
  function createFilePreview(input, previewId) {
    const previewContainer = document.getElementById(previewId);
    
    if (previewContainer) {
      // Clear previous preview
      previewContainer.innerHTML = '';
      
      if (input.files && input.files[0]) {
        const file = input.files[0];
        
        // Create preview element
        const preview = document.createElement('div');
        preview.className = 'file-preview';
        
        // Add file icon based on type
        const fileIcon = document.createElement('i');
        if (file.type === 'application/pdf') {
          fileIcon.className = 'fas fa-file-pdf';
        } else if (file.type.includes('word')) {
          fileIcon.className = 'fas fa-file-word';
        } else if (file.type.includes('text')) {
          fileIcon.className = 'fas fa-file-alt';
        } else {
          fileIcon.className = 'fas fa-file';
        }
        
        // Add file name and size
        const fileInfo = document.createElement('div');
        fileInfo.className = 'file-info';
        fileInfo.innerHTML = `
          <span class="file-name">${file.name}</span>
          <span class="file-size">${formatFileSize(file.size)}</span>
        `;
        
        // Add remove button
        const removeButton = document.createElement('button');
        removeButton.className = 'remove-file-btn';
        removeButton.innerHTML = '<i class="fas fa-times"></i>';
        removeButton.setAttribute('type', 'button');
        removeButton.setAttribute('aria-label', 'Remove file');
        
        removeButton.addEventListener('click', function() {
          // Clear file input
          input.value = '';
          
          // Remove preview
          previewContainer.innerHTML = '';
        });
        
        // Assemble preview
        preview.appendChild(fileIcon);
        preview.appendChild(fileInfo);
        preview.appendChild(removeButton);
        
        // Add to preview container
        previewContainer.appendChild(preview);
      }
    }
  }
  
  /**
   * Format file size
   */
  function formatFileSize(bytes) {
    if (bytes === 0) return '0 Bytes';
    
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  }
  
  /**
   * Clear file upload previews
   */
  function clearFileUploadPreviews() {
    const previewContainers = document.querySelectorAll('.file-preview-container');
    
    previewContainers.forEach(container => {
      container.innerHTML = '';
    });
  }
}

// Initialize the page when DOM is ready
document.addEventListener('DOMContentLoaded', initPage);

export default {
  initPage
};
